.bg-transparent {
    background-color: transparent;
}

a.nav-link {
    font-weight: bold;
    color: white;
    padding: 1rem 2rem;
    outline: none;
}

a.nav-link:hover, .navbar-brand a.nav-link:hover, button.navbar-burger:hover, a.nav-link:focus, .navbar-brand a.nav-link:focus, button.navbar-burger:focus {
    color: black;
    background-color: white;
}

a.is-current, a.nav-link:active, .navbar-brand a.nav-link:active, button.navbar-burger {
    color: #6b3910;
    background-color: #cbbfa9;
}

header.home {
    position: absolute;
    top: 0;
    width: 100%;
}

header.nothome {
    background-color: black;
}